/**
 * 'basic-sans', 300, 300i, 400, 400i, 700, 700i
 * 'bodoni-urw', 400, 400i, 700, 700i
 */
/*
$sans: 'proxima-nova', 'Helvetica Neue', helvetica, sans-serif; */
$sans: 'basic-sans', 'Helvetica Neue', helvetica, sans-serif;
$serif: 'freight-display-pro', Didot, Didot LT STD, Hoefler Text, Garamond,
	Times New Roman, serif;

$h1: 5rem;
$h2: 2.2rem;
$h3: 1.5rem;
$h4: 1.2rem;
$h5: 1.25rem;

body {
	font-family: $sans;
}
h1 {
	font-size: $h1;
}
h2 {
	font-size: $h2;
}
h3 {
	font-size: $h3;
}
h4,
p,
li,
input,
button,
textarea,
select,
label {
	font-size: $h4;
}
p,
li {
	font-family: $sans;
	font-size: $h4;
	line-height: 1.8rem;
}
h5 {
	font-size: $h5;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	letter-spacing: 0.05em;
}

p + p {
	margin-top: 1em;
}

strong {
	font-weight: 700;
}
em {
	font-style: italic;
}

li {
	list-style-type: none;
}
